
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

#tilefish_commentary {
  display: block;
  margin-left: -45px;
}
#tilefish_commentary ul {
  list-style-type: none;
}
#tilefish_commentary li {
  display: block;
}
a {
  color: #42b983;
}
#tilefish_funfacts {
  display: block;
  font-size: 150%;
  text-align: center;
  margin: auto;
  margin-top: 5%;
}
#tilefish_funfacts ol {
  text-align: left;
}
#tilefish_funfacts li {
  margin: auto;
  font-size: 70%;
}
#tilefish_sources {
  display: block;
  margin-left: -45px;
  font-size: 30%;
}
#tilefish_sources ul {
  list-style-type: none;
}
#tilefish_sources li {
  display: block;
}
